import './Weather.css';
import sunrise from '../img/sunrise.png'
import sunset from '../img/sunset.png'
import React from 'react';


class Weather extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      weatherData: this.props.weatherData,
    };
    this.weatherBlock = this.weatherBlock.bind(this);
  }

  timeConvertor(timeStamp) {
    let dateTime = new Date(timeStamp);
    let AMPM = "";
    if (dateTime.getUTCHours() < 12) {
      AMPM = "AM"
    } else {
      AMPM = "PM"
    }
    let Time = dateTime.getUTCHours() + ':' + dateTime.getUTCMinutes() + " " + AMPM;
    return Time;
  }

  dateConvertor(timeStamp) {
    const dayToDate = {1: "Mon",2: "Tue",3: "Wed",4: "Thu",5: "Fri",6: "Sat",7: "Sun"};
    let dateTime = new Date(timeStamp);
    let returnDate = dayToDate[dateTime.getUTCDay()] + " " + dateTime.getUTCDate() + "/" + dateTime.getUTCMonth();
    return(returnDate);
  }

  weatherBlock() {
    // prepare content for current weather
    let wdt = this.state.weatherData;
    let countryCityName = wdt.timezone;
    let countryName = countryCityName.split('/')[0];
    let cityName = countryCityName.split('/')[1];
    let currentTemp = wdt.current.temp;
    let currentWeatherIntro = wdt.current.weather[0].description;
    let currentWeatherIcon = wdt.current.weather[0].icon;
    let currentWeatherIconLink = "http://openweathermap.org/img/wn/" + currentWeatherIcon + "@2x.png";
    let sunriseTime = this.timeConvertor((wdt.current.sunrise + wdt.timezone_offset) * 1000);
    let sunsetTime = this.timeConvertor((wdt.current.sunset + wdt.timezone_offset) * 1000);

    let dayOneTemp = wdt.daily[1].temp.day;
    let dayOneWeatherIntro = wdt.daily[1].weather[0].description;
    let dayOneWeatherIcon = wdt.daily[1].weather[0].icon;
    let dayOneWeatherIconLink = "http://openweathermap.org/img/wn/" + dayOneWeatherIcon + ".png";
    let sunriseTimeDayOne = this.timeConvertor((wdt.daily[1].sunrise + wdt.timezone_offset) * 1000);
    let sunsetTimeDayOne = this.timeConvertor((wdt.daily[1].sunset + wdt.timezone_offset) * 1000);
    let dayOneDate = this.dateConvertor((wdt.daily[1].sunrise + wdt.timezone_offset) * 1000);

    let dayTwoTemp = wdt.daily[2].temp.day;
    let dayTwoWeatherIntro = wdt.daily[2].weather[0].description;
    let dayTwoWeatherIcon = wdt.daily[2].weather[0].icon;
    let dayTwoWeatherIconLink = "http://openweathermap.org/img/wn/" + dayTwoWeatherIcon + ".png";
    let sunriseTimeDayTwo = this.timeConvertor((wdt.daily[2].sunrise + wdt.timezone_offset) * 1000);
    let sunsetTimeDayTwo = this.timeConvertor((wdt.daily[2].sunset + wdt.timezone_offset) * 1000);
    let dayTwoDate = this.dateConvertor((wdt.daily[2].sunrise + wdt.timezone_offset) * 1000);

    let dayThreeTemp = wdt.daily[3].temp.day;
    let dayThreeWeatherIntro = wdt.daily[3].weather[0].description;
    let dayThreeWeatherIcon = wdt.daily[3].weather[0].icon;
    let dayThreeWeatherIconLink = "http://openweathermap.org/img/wn/" + dayThreeWeatherIcon + ".png";
    let sunriseTimeDayThree = this.timeConvertor((wdt.daily[3].sunrise + wdt.timezone_offset) * 1000);
    let sunsetTimeDayThree = this.timeConvertor((wdt.daily[3].sunset + wdt.timezone_offset) * 1000);
    let dayThreeDate = this.dateConvertor((wdt.daily[3].sunrise + wdt.timezone_offset) * 1000);

    return (
      <div className="weatherBlock">

        <div className="todayWeather">
          <div className="nameTemp">
            <div style={{ fontSize: 'xx-large' }}>{cityName}</div>
            <div style={{ fontSize: 'medium' }}>{countryName}</div>
            <div style={{ fontSize: 'medium' }}>{currentTemp}째C</div>
          </div>
          <div className="weatherCondition">
            <div className="currentImage"><img src={currentWeatherIconLink} /></div>
          </div>
          <div className="riseSetTime">
            <div className="currentSunrise">
              <div className="sunriseImg"><img height="100%" src={sunrise} /></div>
              <div className="sunriseText" style={{ fontSize: 'medium' }}>{sunriseTime}</div>
              <div style={{ clear: 'both' }}></div>
            </div>
            <div className="currentSunset">
              <div className="sunsetImg"><img height="100%" src={sunset} /></div>
              <div className="sunsetText" style={{ fontSize: 'medium' }}>{sunsetTime}</div>
              <div style={{ clear: 'both' }}></div>
            </div>
            <div style={{ clear: 'both' }}></div>
          </div>
          <div style={{ clear: 'both' }}></div>
        </div>

        <div className="futureWeather">

          <div className="dayOneWeather">
            <div className="dayOneText">
              <div style={{ fontSize: 'medium' }}>{dayOneDate}</div>
              <div style={{ fontSize: 'medium' }}>{dayOneTemp}째C</div>
            </div>

            <div className="futureWeatherImg">
              <img height="100%" src={dayOneWeatherIconLink} />
            </div>

            <div className="futureSunrise">
              <div className="futureSunriseImg">
                <img height="100%" src={sunrise} />
              </div>
              <div className="futureSunriseText">
                {sunriseTimeDayOne}
              </div>
              <div style={{ clear: 'both' }}></div>
            </div>

            <div className="futureSunset">
              <div className="futureSunsetImg">
                <img height="30px" src={sunset} />
              </div>
              <div className="futureSunsetText">
                {sunsetTimeDayOne}
              </div>
            </div>
          </div>

          <div className="mobileBreakLine"></div>

          <div className="dayTwoWeather">
            <div className="dayTwoText">
              <div style={{ fontSize: 'medium' }}>{dayTwoDate}</div>
              <div style={{ fontSize: 'medium' }}>{dayTwoTemp}째C</div>
            </div>
            <div className="futureWeatherImg">
              <img height="100%" src={dayTwoWeatherIconLink} />
            </div>

            <div className="futureSunrise">
              <div className="futureSunriseImg">
                <img height="100%" src={sunrise} />
              </div>
              <div className="futureSunriseText">
                {sunriseTimeDayTwo}
              </div>
              <div style={{ clear: 'both' }}></div>
            </div>

            <div className="futureSunset">
              <div className="futureSunsetImg">
                <img height="30px" src={sunset} />
              </div>
              <div className="futureSunsetText">
                {sunsetTimeDayTwo}
              </div>
            </div>
          </div>

          <div className="mobileBreakLine"></div>

          <div className="dayThreeWeather">
            <div className="dayThreeText">
              <div style={{ fontSize: 'medium' }}>{dayThreeDate}</div>
              <div style={{ fontSize: 'medium' }}>{dayThreeTemp}째C</div>
            </div>

            <div className="futureWeatherImg">
              <img height="100%" src={dayThreeWeatherIconLink} />
            </div>

            <div className="futureSunrise">
              <div className="futureSunriseImg">
                <img height="100%" src={sunrise} />
              </div>
              <div className="futureSunriseText">
                {sunriseTimeDayThree}
              </div>
              <div style={{ clear: 'both' }}></div>
            </div>

            <div className="futureSunset">
              <div className="futureSunsetImg">
                <img height="30px" src={sunset} />
              </div>
              <div className="futureSunsetText">
                {sunsetTimeDayThree}
              </div>
            </div>
          </div>

          <div style={{ clear: 'both' }}></div>
        </div>
      </div>
    )
  }

  render() {
    return (
      <this.weatherBlock />
    )
  }
}

export default Weather;
