import './Animation.css';
import sunrise from '../img/sunrise.png'
import React from 'react';


class Animation extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            weatherData: this.props.weatherData,
            weatherMain: null,
        };
        this.animationBlock = this.animationBlock.bind(this);
        this.rainDrop = this.rainDrop.bind(this);
    }

    rainDrop() {
        let viewWidth = window.innerWidth;
        let viewHeight = window.innerHeight;
        let rainDropIndex = Array.from(Array(99).keys());

        return (
            <React.Fragment>
                {rainDropIndex.map(index => {
                    let thisLeft = Math.floor(Math.random() * viewWidth);
                    let thisTop = Math.floor(Math.random() * viewHeight) - 150;
                    let delay = Math.random();
                    return(<div key={"drop"+index} className="rainDrop" style={{left: thisLeft+"px", top: thisTop+"px", animationDelay:delay+"s"}}></div>)
                })}
            </React.Fragment>
        )
    }

    componentDidMount() {
        let wdt = this.state.weatherData;
        let weaMain = wdt.current.weather[0].main;
        this.setState({ weatherMain: weaMain });
    }

    animationBlock() {

        let weatherBackground = "";
        let landscapeType = "";

        let wdt = this.state.weatherData;
        let currentTime = new Date(wdt.current.dt * 1000 + wdt.timezone_offset * 1000);
        let currentHour = currentTime.getUTCHours();
        let sunriseTime = (new Date(wdt.current.sunrise * 1000 + wdt.timezone_offset * 1000)).getUTCHours();
        let sunsetTime = (new Date(wdt.current.sunset * 1000 + wdt.timezone_offset * 1000)).getUTCHours();

        if (currentHour > sunriseTime && currentHour < sunsetTime) {
            weatherBackground = "skyDay";
            landscapeType = "landscapeDay";
        } else {
            weatherBackground = "skyNight";
            landscapeType = "landscapeNight"
        }

        return (
            <React.Fragment>
                {this.state.weatherMain === "Rain" && <div className="rain">{this.rainDrop()}</div>}
                <div className={landscapeType}></div>
                <div className={weatherBackground}></div>
                {this.state.weatherMain === "Clouds" && <div className="cloud"></div>}
            </React.Fragment>
        )
    }

    render() {
        return (
            <this.animationBlock />
        )
    }
}

export default Animation;
