import './App.css';
import React from 'react';
import Weather from './components/Weather';
import Animation from './components/Animation';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isFetched: false,
      weatherData: null,
    };
    this.positionGetWeather = this.positionGetWeather.bind(this);
  }

  componentDidMount() {
    navigator.geolocation.getCurrentPosition(this.positionGetWeather);
  }

  positionGetWeather(position) {
    let clientLat = position.coords.latitude;
    let clientLon = position.coords.longitude;
    fetch('https://api.openweathermap.org/data/2.5/onecall?lat=' + clientLat + '&lon=' + clientLon + '&units=metric&appid=18672d763ea1df4d4b293153144f713b', { method: 'GET' })
      .then(response => response.json())
      .then(result => {
        this.setState({ isFetched: true, weatherData: result })
      })
  }

  render() {
    if (this.state.isFetched === false) {
      return (
        <div>Loading</div>
      )
    } else if (this.state.isFetched === true) {
      return (
        <React.Fragment>
          <div className="weatherHolder">
            <Weather weatherData={this.state.weatherData} />
          </div>
          <div className="animationHolder">
            <Animation weatherData={this.state.weatherData} />
          </div>
        </React.Fragment>
      )
    }
  }
}

export default App;
